.question_box .question_text {
    position: relative;

    z-index: 2;
    font-size: 18px;

    font-weight: 600;

    padding-top: 7.5rem;
    padding-bottom: .2rem;
}

.question_box .group_name {
    position: relative;
    z-index: 2;

    font-size: 12px;
}

.question_box img {
    position: absolute;

    width: 350px;

    top: -160px;
    left: 0;
}

.image-result {
    width: 300px;
    position: absolute;
    left: 50%;
    bottom: 0;

    transform: translate(-50%, 0);
}

.div-center {
    display: table;
    margin: 0 auto;
}

.div-answer-buttons {
    margin-top: 1rem
}